/*Scrollbar*/
::-webkit-scrollbar {
  border-radius: 4px;
  width: 8px;
}
/*Handle*/
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(141, 141, 141, 0.3);
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(141, 141, 141, 0.9);
}
/*Track*/
::-webkit-scrollbar-track {
  border-radius: 4px;
  background: transparent;
}
